<script>
import { defineComponent, ref, computed } from 'vue'
import moment from 'moment'
import { useRouter } from 'vue-router'
import { useUser } from 'composable/user'
import { money } from 'utils/number'
import { useBuybackPlan } from 'composable/setting'
import { useCancelSubscription } from 'composable/billing'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Currency from 'components/Currency.vue'
import CollapsibleSection from 'components/Plan/CollapsibleSection.vue'

export default defineComponent({
  components: {
    Button,
    Card,
    Checkbox,
    Modal,
    Alert,
    Input,
    Select,
    Currency,
    CollapsibleSection
  },
  async setup(props, { emit }) {
    const {
      packages: availablePackages,
      loading,
      actionAll,
      actionUpgrade,
      actionCheckout,
      stateCheckout
    } = useBuybackPlan()
    const { me, isTrialEnd, isCanceledSubscription } = useUser()
    const {
      data: formCancelSubscription,
      loading: loadingCancelSubscription,
      action: actionCancelSubscription
    } = useCancelSubscription()
    const router = useRouter()
    const modal = ref({
      confirmation: false,
      status: false,
      cancel_subscription: false
    })
    const payload = ref({
      price: '',
      price_id: ''
    })
    const buttonLoading = ref([false, false, false, false])
    const cancelReasons = ref([
      {
        name: 'Not Useful',
        value: 'Not Useful'
      },
      {
        name: 'Bad Interface',
        value: 'Bad Interface'
      },
      {
        name: 'Bad Support',
        value: 'Bad Support'
      },
      {
        name: 'Lack features',
        value: 'Lack features'
      },
      {
        name: 'Other',
        value: 'Other'
      }
    ])
    const isShowPassword = ref(false)

    const goToLocation = () => {
      router.push({ name: 'settingManageLocation' })
    }

    const showIntercom = () => {
      window.CustomerSupport.use('show')
    }
    const buttonAction = (item, index) => {
      if (item.is_active !== 1) showIntercom() // if grow & enterprise & accelerate package
    }
    const buttonModal = (item, index) => {
      payload.value.price = item.price
      payload.value.price_id = item.stripe_product_id

      if (me.value.package_id === 1 || isTrialEnd.value === 1) {
        buttonLoading.value[index] = true
        upgradeCheckout(payload.value.price_id)
        return
      }
      modal.value.confirmation = true
    }
    const resetButtonLoading = () => {
      buttonLoading.value = [false, false, false, false]
    }
    const upgradePlan = async (price_id) => {
      await actionUpgrade({ price_id })
      modal.value.confirmation = false
      modal.value.status = true
      setTimeout(() => location.reload(), 800)
    }
    const upgradeCheckout = async (price_id) => {
      await actionCheckout({ price_id })
      modal.value.confirmation = false
      modal.value.status = true
      window.location.replace(stateCheckout.value.url)
      resetButtonLoading()
    }
    const handleCancelSubscription = () => {
      modal.value.cancel_subscription = true
    }
    const handleSubmitCancelSubscription = async () => {
      await actionCancelSubscription(() => {
        location.reload()
      })
    }

    const excludePackages = {
      names: ['FREE FOREVER', 'LOCAL', 'Additional Location'],
      ids: [1, 2, 5]
    }

    const activeSubsRemaining = computed(() =>
      me.value.subscription_period_end ?  moment(
        me.value.subscription_period_end,
        'YYYY-MM-DD HH:mm:ss'
      ).diff(moment(), 'days') : -1 
    )
    const trialEndDate = computed(() =>
      !!me.value?.trial_end
        ? moment(me.value.trial_end, 'YYYY-MM-DD HH:mm:ss').format(
            me.value?.date_format
          )
        : ''
    )
    const packages = computed(() =>
      availablePackages.value.filter(
        (packageDetail) =>
          !(
            excludePackages.names.includes(packageDetail.name) ||
            excludePackages.ids.includes(packageDetail.id)
          )
      )
    )
    const plan = computed(() =>
      packages.value.filter((element) => element.is_addon == 0)
    )
    const addOn = computed(() =>
      packages.value.filter((element) => element.is_addon == 1)
    )

    await actionAll()

    return {
      plan,
      modal,
      payload,
      loading,
      loadingCancelSubscription,
      addOn,
      cancelReasons,
      formCancelSubscription,
      isShowPassword,
      buttonLoading,
      goToLocation,
      buttonAction,
      buttonModal,
      upgradePlan,
      upgradeCheckout,
      showIntercom,
      me,
      money,
      activeSubsRemaining,
      trialEndDate,
      isCanceledSubscription,
      isTrialEnd,
      handleCancelSubscription,
      handleSubmitCancelSubscription
    }
  }
})
</script>

