<template>
  <div :class="['collapsible-section', ...collapsibleClasses]">
    <div @click="toggleCollapse" class="collapsible-section__header">
      <slot name="title"></slot>
      <img v-if="!hideContent" class="collapsible-arrow" src="@/assets/icon/arrow-circle.svg" alt="arrow">
    </div>
    <div v-show="!hideContent" class="collapsible-section__content">
      <p ref="refContent" class="text-sm">
        <slot></slot>
      </p>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, computed, onMounted, watch, onUnmounted } from 'vue'
export default defineComponent({
  name: 'CollapsibleSection',
  props: {
    wordLimit: {
      type: Number,
      default: 12
    },
    hideContent: {
      type: Boolean,
      default: false
    }
  },
  setup(props, {emit}) {
    const isCollapse = ref(false)
    const refContent = ref(null)
    const originalContent = ref('')

    const toggleCollapse = () => {
      isCollapse.value = !isCollapse.value
    }
    const collapsibleClasses = computed(() => {
      const classes = []
      if(isCollapse.value) classes.push('collapsible-section--collapse')
      return classes
    })
    
    onMounted(() => {
      const content = refContent.value
      if(!!content){
        originalContent.value = content.innerHTML
      
        const wordLength = originalContent.value.split(' ').length
        if(isCollapse.value || (wordLength <= props.wordLimit)) content.innerHTML = originalContent.value
        else content.innerHTML = originalContent.value.split(' ').filter((_, wordIndex) => wordIndex < props.wordLimit).join(' ') + ' ...'
      }
    })

    const destroyWatcher = watch(() => isCollapse.value, (isCollapse) => {
      const wordLength = originalContent.value.split(' ').length
      if(isCollapse || (wordLength <= props.wordLimit)) refContent.value.innerHTML = originalContent.value
      else refContent.value.innerHTML = originalContent.value.split(' ').filter((_, wordIndex) => wordIndex < props.wordLimit).join(' ') + ' ...'
    }, {
      immediate: false
    })

    onUnmounted(() => destroyWatcher())

    return {
      originalContent,
      refContent,
      isCollapse,
      toggleCollapse,
      collapsibleClasses
    }
  }
})
</script>
<style lang="scss" scoped>
.collapsible-section{
  @apply rounded-lg bg-white ;
  .collapsible-section__header,
  .collapsible-section__content{
    @apply p-3 bg-grey-lighten;
  }
  .collapsible-section__header{
    @apply flex justify-between items-center p-3 cursor-pointer border-grey-01 select-none bg-grey-lighten;
  }
  .collapsible-arrow{
    @apply transition-transform duration-300 transform rotate-0;
  }
  &.collapsible-section--collapse{
    .collapsible-arrow{
      @apply rotate-180;
    }
  }
}
</style>